import {Injectable} from "angular2/core";
import {Task} from "../models/task";
import {Http, Headers, HTTP_PROVIDERS} from 'angular2/http';
import {Observable} from 'rxjs/Observable';
import 'rxjs/add/operator/share';
import 'rxjs/add/operator/map';
import {Ng2NotifyService} from 'ng2-notify/notify';
import {TodoService} from "./service";

var apiUrl = 'http://todo-node-api.dimaslz.io';
declare var io: any;

@Injectable()
export class SocketService {
    socket$: Observable<Array<Task>>;
    private _socketObserver: any;
    public socket: any;
    
    constructor(private notification: Ng2NotifyService, private todoService: TodoService) {
        this.socket = io('http://192.168.1.128:3000');
        this.socket$ = new Observable(observer => this._socketObserver = observer).share();
    };
    
    public reloadList(type:string, message) {
        this.todoService.reloadList();
        this.notification.show(type, message);
    }
}